@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --primary-white: #fff;
  --primary-pink: #FF69B4;
  --primary-dark: rgba(255, 255, 255, 0);
}

body {
  font-family: "Roboto Condensed", sans-serif;
}

/*** HEADER/NAVBAR ***/
nav a.nav-link {
  font-weight: 500;
  letter-spacing: 00.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid rgba(255, 255, 255, 0);
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-brand {
  color: #fff !important;
  color: var(--primary-white) !important;
  font-family: "Roboto Condensed", sans-serif !important;

}

.navbar-nav > li > a.active, .navbar-nav > li > a.active:focus {
  border-top: 0.1875rem solid #FF69B4;
  border-top: 0.1875rem solid var(--primary-pink);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #FF69B4;
  border-top: 0.1875rem solid var(--primary-pink);
}

/*** HOME ***/

.header-wrapper {
  position: relative;
  background: url("https://media2.giphy.com/media/l3q2vQ6rlZutXsYN2/giphy.gif?cid=ecf05e47jfjjvdqae2m7i13era78s8m740f8ewck3qazxrl5&rid=giphy.gif");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 90vh;
}

.main-info {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

.main-info h1 {
  color: #fff;
  color: var(--primary-white);
  text-transform: uppercase;
}

@media(max-width: 360px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }
}

.btn-main-offer {
  border: 3px solid #FF69B4;
  border: 3px solid var(--primary-pink);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: #fff;
  color: var(--primary-white);
  margin: 2rem;
  font-weight: bold;
  cursor: pointer;
}

@media(max-width: 360px) {
  .header-wrapper {
    background-color: black;
  }
}

/*** CONTACT ***/

#contact {
  margin-top: 5rem;
  margin-left: 10rem;
  margin-right: 10rem;
}

#contact-form {
  margin-bottom: 10rem;
}

/*** ABOUT ***/

#about {
  margin-top: 5rem;
  margin-left: 10rem;
  margin-right: 10rem;
}

.headshot {
  width: 25rem;
  height: auto;
  margin-bottom: 10rem;
}

/*** TESTIMONIALS ***/

#testimonials {
  margin-top: 5rem;
  margin-left: 10rem;
  margin-right: 10rem;
  font-style: italic;
}

#testimonialName {
  font-weight: bold;
}

/*** PORTFOLIO ***/

#portfolio {
  margin-top: 3rem;
  margin-left: 10rem;
  margin-right: 10rem;
  margin-bottom: 5rem;
}

.card {
  margin-bottom: 3rem;
}

/*** STICKY FOOTER ***/

.footer{
  margin-top: 1rem;
  padding: 1rem;
  background-color: #00FFFF;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}
